#resume {
    width: 960px;
    padding: 2rem;
    margin: auto;
    border: 1px #ccc solid;
    border-radius: 1.2rem;
    background-color: #FFF;
    box-shadow: 3px 3px 5px #ccc;
}

@media only screen and (max-width: 756px) {
    #resume {
        width: auto;
        margin: 1rem auto;
    }
}